
*{
  font-family: 'Quicksand';
}
.containProject {
  display: grid;
  grid-template-columns: 200px 200px 200px 200px 100px;
  grid-template-rows: repeat(50,20px);
  margin-left: auto;
  margin-right: auto;
  margin-top: 80px; 
  height: 1000px;
  width: 800px;
  /* background-color: #ffffff;
  border-radius: 10%;
  box-shadow: 0px 0px 10px 5px #86679a; */
  color:rgb(0, 0, 0);
  color:#5d5c5c;
}
.containProject h1{
  grid-column-start: 1;
  grid-column-end:5;
  grid-row-start: 22;
  font-weight:bold;
  font-size: 200%;  
  padding: 30px;
  margin-left: 4%;
}
.containProject h2{
  grid-column-start: 1;
  grid-column-end:5;
  grid-row-start: 26;
  font-size: 100%;  
  margin-left: 8%;  
}
.containProject p{
  grid-row-start:28;
  grid-column-start: 1;
  grid-column-end:5;
  font-size: 100%;  
  margin-left: 8%;  
} 
.style1 {  
  width: 90%;
  height: 2000%;
  grid-column-start: 1;
  grid-column-end: 5;
  z-index: 2;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  box-shadow: 0px 0px 50px 25px rgb(153, 153, 153);
}
