*{
  margin:0;
  font-family: 'Quicksand'
}
.content {
  font-weight: bold;
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
.project-list h1{
  font-weight: 1000;
  font-size: 250%;  
  
}
.project-preview {
  margin-left: auto;
  margin-right: auto;
  padding: 10px 16px;
  margin: 20px 0;
  background-color: #c6cec1;
  border-bottom: 10px solid #585858;
  box-shadow: 3px 10px 3px rgb(153, 153, 153);
  border-radius: 20px;
}
.project-preview:hover {
  box-shadow: 3px 10px 3px #5c3a72;
}
.project-preview h2 {
  background-color: #c6cec1;
  font-size: 25px;
  font-weight: bold;
  color: #384b2d;
}
.project-preview a {
  text-decoration: none;
  color: #5c3a72;
}
.project-preview p {
  font-weight: 450;
  background-color: #c6cec1;
  text-decoration: none;
}