@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* targets every element*/
body {
  background-color: #EBEDDF;
}
* {
  margin: 0;
  font-family: "Quicksand";
  font-weight: 400;
}
.navbar {
  padding: 15px;
  align-items: center;
  margin: 0 auto;
  max-width: 100%;
  background-color: #384b2d;
  border-bottom: 1px solid #1F2B37;
}

.navbar .links {
  text-align: center;
}
.navbar a {
  text-decoration: none;
  color: #EBEDDF;
  font-weight: 800;
  padding: 6px;
}
.navbar a:hover{
  color: #86679a;
}
.navbar .icons{
  margin-left: auto;
}